.intro__wrapper {
  background-image:url(../img/intro-triangle.svg), url(../img/jpg/back-mobile.jpg);
  background-repeat: no-repeat;
  background-position: 0px 332px, 0px 0px;
  height: 389px;
  text-align: center;
  margin: 0;
  padding: 0;
}

.intro__image {
  margin: 0;
  padding: 0;
  margin-top: 90px;
}

.intro__subject {
  margin: 0;
  padding: 0;
  padding: 39px 18px 17px 17px;
  text-align: center;
  text-transform: uppercase;
}

.intro__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  margin: 0;
  padding: 0;
  margin-bottom: 35px
}

.intro__text {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .intro__subject {
    margin: 0;
    padding: 0;
    padding: 47px 124px 56px 124px;
    text-align: center;
    text-transform: uppercase;
  }
}

@media (min-width: $desktop-width) {
  .intro__subject {
    margin: 0;
    padding: 0;
    padding: 59px 358px 45px 358px;
    text-align: center;
    text-transform: uppercase;
  }
}
